<template>
  <van-popup v-model="show" position="bottom">
    <van-picker ref="picker" title="标题" show-toolbar :columns="columns" @confirm="onConfirm" @cancel="onCancel" :value-key="valueKey" />
  </van-popup>
</template>
<script>

import { Popup, Picker } from 'vant'

export default {
  name: 'pickerPopup',
  components: {
    [Popup.name]: Popup,
    [Picker.name]: Picker
  },
  data () {
    return {
      show: false,
      columns: []
    }
  },
  props: {
    datas: {
      type: Array,
      default: () => {
        return []
      }
    },
    valueKey: {
      type: String,
      default: 'text'
    }
  },
  methods: {
    setList (list) {
      this.columns = list
    },
    showPopup (index) {
      this.show = !this.show
      if (index) {
        this.$nextTick(res => {
          this.$refs.picker.setIndexes(index)
        })
      }
    },
    onConfirm (item, index) {
      this.show = false
      this.$emit('confirm', item, index)
    },
    onCancel () {
      this.show = false
    }
  },
  mounted () {

  },
  watch: {
    datas () {
      if (this.datas.length > 0) {
        this.columns = this.datas
      }
    }
  }
}
</script>
<style lang="scss" scoped>

</style>
