<template>
  <div class='root'>
    <action-bar title='修改商品价格、库存'></action-bar>
    <div class="batch-price" v-if="goodsList.length > 1">
      <van-radio-group v-model="check" direction="horizontal">
        <van-radio name="up" checked-color="#0e932e" icon-size="16px">批量加价</van-radio>
        <van-radio name="down" checked-color="#0e932e" icon-size="16px">批量降价</van-radio>
      </van-radio-group>
      <div class="batch-money">
        <span>¥</span>
        <div class="batch-input-box">
          <input type="number" class="batch-input" v-model="batchPrice" />
        </div>
        <span>元</span>
        <div class="batch-sure" @click="bindBatchSure">确定</div>
      </div>
    </div>
    <div class='container'>
      <scroll-view class='content' :data="goodsList">
        <goods-item v-for="(item, index) in goodsList" :key="item.productId">
          <van-image slot="imgSlot" radius=".25rem" :src="baseUrl + item.imageUrl" class="goods-img" />
          <div slot="nameSlot" class="goods-title">{{ item.productName }}</div>
          <div slot="attributeSlot">
            <div class="attribute">
              <div class="attribute-content">颜色：{{ item.color }}</div>
              <div class="attribute-content">规格：{{ item.unit }}</div>
              <div class="attribute-content">等级：{{ item.level }}</div>
            </div>
            <div class="attribute">
              <div class="attribute-content">可售库存：{{ item.stock }}</div>
              <div class="attribute-content">下单冻结：{{ item.stockFreeze }}</div>
            </div>
          </div>
          <div slot="menuSlot" class="goods-bottom">
            <div class="price-stock">
              <div>价格：</div><input maxlength="10" type="number" v-model="item.priceStay" class="input-bg margin-right"
                :placeholder="item.price" />
            </div>
            <div class="price-stock">
              总库存：<input type="number" v-model="item.stockStay" :placeholder="item.stockTotal" maxlength="10"
                class="input-bg" />
            </div>
            <div class="price-stock" @click="showStockPopup(index,item.reasonIndex)">
              <div
                :class="['price-stock-content', item.reasonIndex == undefined || item.reasonIndex == 0 ? 'empty' : '']">
                {{
                  item.reasonIndex == undefined || item.reasonIndex == 0 ? '修改库存原因'
                    : reasonList[item.reasonIndex].text
                }}
                <img src="@/assets/arrow_down_icon.png" class="empty-img" />
              </div>
            </div>
          </div>
        </goods-item>
      </scroll-view>
    </div>
    <div class="bottom">
      <div class="button-normal" @click="handleSubmit">提交</div>
    </div>
    <picker-popup ref="stockPopup" @confirm="choiceStockType"></picker-popup>
  </div>
</template>
<script>
import { RadioGroup, Radio, Dialog } from 'vant'
import goodsItem from '@/components/util/GoodsItem.vue'
import { editProductPriceStockBatch, productDetailsList } from '@/http/api'

import pickerPopup from '@/components/util/PickerPopup.vue'

export default {
  name: 'updatePriceStock',
  components: {
    goodsItem,
    pickerPopup,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Dialog.name]: Dialog.Component
  },
  data () {
    return {
      goodsData: [], // 商品选择信息，id，位置
      goodsList: [], // 商品信息
      check: '',
      batchPrice: '',
      reasonList: [{
        id: 0,
        text: '无'
      },
      {
        id: 3,
        text: '报销库存'
      }, {
        id: 4,
        text: '遗失库存'
      }, {
        id: 5,
        text: '线下售卖'
      }],
      stockIndex: 0
    }
  },
  methods: {
    choiceStockType (data, index) {
      const item = this.goodsList[this.stockIndex]
      item.stockType = data.id
      item.reasonIndex = index
      this.$forceUpdate()
    },
    bindBatchSure () {
      const check = this.check
      if (!check) {
        this.$toast('请选择批量加价或降价')
        return
      }
      const batchPrice = this.batchPrice
      if (!batchPrice) {
        this.$toast('请输入金额')
        return
      }
      let price = 0
      if (check === 'up') {
        price += parseFloat(batchPrice)
      } else {
        price -= parseFloat(batchPrice)
      }
      // 逻辑为所有商品进行批量加价/降价
      const goodsList = this.goodsList
      let ct = ''
      for (let i = 0; i < goodsList.length; i++) {
        const item = goodsList[i]
        let p = ''
        if (item.priceStay !== undefined) {
          p = parseFloat(item.priceStay) + price
        } else {
          p = parseFloat(item.price) + price
        }
        if (p >= 0) {
          item.priceStay = p
        } else {
          ct = ct + item.productName + '\n'
        }
      }
      if (ct) {
        Dialog.alert({
          title: '降价成功，以下商品降价后价格低于0元',
          message: ct
        }).then(() => {
          // on close
        })
      }
      this.goodsList = goodsList
      let toast = ''
      if (check === 'up') {
        toast = '加价成功，请提交生效'
      } else {
        toast = '降价成功，请提交生效'
      }
      this.$toast(toast)
      this.$forceUpdate()
    },
    showStockPopup (index, pickerIndex) {
      this.stockIndex = index
      const pIndex = pickerIndex || 0
      this.$refs.stockPopup.showPopup([pIndex])
      this.$forceUpdate()
    },
    // 提交
    handleSubmit () {
      const goodsList = this.goodsList
      if (goodsList.length === 0) {
        return
      }
      // 增加提交校验，必须有修改才可提交
      for (const elt of goodsList) {
        if (!elt.priceStay && !elt.stockStay) {
          this.$toast('<' + elt.productName + '>价格或库存必须填写一项')
          return
        }
      }
      const data = []
      goodsList.forEach(elt => {
        data.push({
          productId: elt.productId,
          price: (!elt.priceStay) ? elt.price : elt.priceStay,
          stock: (!elt.stockStay) ? elt.stockTotal : elt.stockStay,
          stockType: elt.stockType !== '' ? elt.stockType : 0
        })
      })
      editProductPriceStockBatch({
        data: JSON.stringify(data)
      }).then(res => {
        const goodsData = this.goodsData
        const goodsList = this.goodsList
        for (let index = 0; index < goodsData.length; index++) {
          const gd = goodsData[index]
          const gl = goodsList[index]
          gd.price = (!gl.priceStay) ? gl.price : gl.priceStay
          gd.stock = (!gl.stockStay) ? gl.stock : gl.stockStay
        }
        this.$route.params.goodsData = goodsData
        this.$router.go(-1)
        this.$toast.success('修改成功')
      }).catch(res => {
        this.$toast(res.msg)
      })
    }
  },
  mounted () {
    this.goodsData = this.$route.query.data
    let productIds = ''
    const data = this.goodsData
    for (let index = 0; index < data.length; index++) {
      productIds += data[index].productId
      if (index < data.length - 1) {
        productIds += ','
      }
    }
    // 获取商品信息
    productDetailsList({
      productIds
    }).then(res => {
      this.goodsList = res.data.result
    }).catch(res => {
      this.$toast(res.msg)
    })

    this.$refs.stockPopup.setList(this.reasonList)
  }
}
</script>
<style  scoped>
.root {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.container {
  flex-grow: 1;
  position: relative;
}

.content {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.bottom {
  height: 3.4375rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: .375rem;
  box-sizing: border-box;
}

.button-normal {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: #42D18A;
  border-radius: .25rem;
  font-weight: 500;
  width: 100%;
  height: 100%;
  font-size: .875rem;
}

.goods-img {
  width: 20vw;
  height: 20vw;
  border-radius: .25rem;
  background-color: #F2F2F2;
  margin-right: .25rem;
}

.goods-title {
  font-size: .875rem;
}

.attribute {
  display: flex;
  flex-direction: row;
  margin-top: .375rem;
}

.attribute-content {
  flex-grow: 1;
  font-size: .75rem;
}

.goods-bottom {
  display: flex;
  flex-direction: row;
  padding-left: .625rem;
  padding-right: .625rem;
  padding-top: .625rem;
  padding-bottom: .3125rem;
  border-bottom: #F5F5F5 solid .375rem;
}

.price-stock {
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  width: 33.3%;
  white-space: nowrap;
  align-items: center;
  justify-content: center;
  font-size: .875rem;
}

.input-bg {
  background-color: #f5f7f9;
  border-radius: .3125rem;
  padding-left: .375rem;
  padding-right: .375rem;
  padding-top: .375rem;
  padding-bottom: .375rem;
  width: 50%;
  border: none;
}

.input-bg::placeholder {
  color: #888F9F;
}

.margin-right {
  margin-right: .875rem;
}

.empty-img {
  width: .75rem;
  height: auto;
  margin-left: .375rem;
}

.price-stock-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.empty {
  color: #999;
}

.batch-price {
  background: #42D18A;
  border-radius: .25rem;
  color: white;
  padding: .25rem;
  display: flex;
  margin: .5rem;
  justify-content: space-between;
}

.batch-money {
  display: flex;
  font-size: .9375rem;
  align-items: center;
}

.batch-input-box {
  border-bottom: .0625rem white solid;
}

.batch-input {
  width: 3.125rem;
  padding: .25rem;
  background: #42D18A;
  text-align: center;
  border: none;
}

.root>>>.van-radio__label {
  color: white;
  font-size: .875rem;
}

.batch-sure {
  border-radius: .25rem;
  height: 1.5625rem;
  padding: 0 .8125rem;
  border: .0625rem white solid;
  line-height: 1.5625rem;
  margin-left: .8125rem;
  font-size: .875rem;
  white-space: nowrap;
}

.root>>>.van-dialog {
  z-index: 1000;
}
</style>
